body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: #222;
}

.container {
  width: 90vmin;
  height: 90vmin;
  background: #f0f;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: rotator 5s linear infinite;
}

.thingy {
  position: relative;
  background: #fff;
  border: 1px solid #000;
  width: 80%;
  height: 80%;
}

.thingy > .thingy {
  position: absolute;
  transform: translate(25%, 25%) rotate(30deg);
}

@keyframes rotator {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}